import "./subcomponent.css";
var a = 0;
async function access() {
  const res = await fetch("https://reqres.in/api/users?page=1");
  const datas = await res.json();
  datas.data.map((x) => {
    console.log(x);
    const outer = document.createElement("div");
    const Name = document.createElement("p");
    const Email = document.createElement("p");
    Email.append("Email: ");
    Email.append(x.email);
    Name.append("Name: ");
    Name.append(x.first_name);
    Name.append(" ");
    Name.append(x.last_name);
    const image = document.createElement("img");
    image.src = x.avatar;
    image.classList.add("sitaji");
    outer.append(image);
    outer.append(Name);
    outer.append(Email);
    outer.classList.add("ramji");
    abc.append(outer);

    a = a + 1;
  });
  load();
}
function load() {
  if (!abc.firstChild) {
    target.classList.add("loading");
  } else {
    target.classList.remove("loading");
  }
}
function both() {
  if (a == 0) {
    access();
    load();
  }
}

export default function Subcomponent() {
  return (
    <div>
      <div id="navbar">
        <button onClick={both}>Get Users</button>
      </div>
      <div id="loadingcover">
        <div id="target"></div>
      </div>
      <div>
        <div id="abc"></div>
      </div>
    </div>
  );
}
